// 키프레임
@keyframes cubes {
  0%{transform: rotate(0deg) translate(10%, 10%);}
  25%{transform: rotate(10deg) translate(10%, 10%);}
  50%{transform: rotate(0deg) translate(10%, 10%);}
  75%{transform: rotate(-10deg) translate(10%, 10%);}
  100%{transform: rotate(0deg) translate(10%, 10%);}
}
@keyframes cubeop {
  0%{opacity: 0;}
  25%{opacity: 1;}
}

@keyframes cubeg1 {
  0%{transform: translate(0px);}
  50%{transform: translate(700px, -500px);}
  100%{transform: translate(0px);}
}
@keyframes cubeg22 {
  0%{transform: translate(0px);}
  50%{transform: translate(400px, 400px);}
  100%{transform: translate(0px);}
}
@keyframes cubeg15 {
  0%{transform: translateY(0px);}
  50%{transform: translateY(-600px);}
  100%{transform: translateY(0px);}
}
@keyframes cubeg21 {
  0%{transform: translate(0px);}
  50%{transform: translate(400px, 300px);}
  100%{transform: translate(0px);}
}

@keyframes cubeg7 {
  0%{transform: translate(0px);}
  50%{transform: translate(-400px, 200px);}
  100%{transform: translate(0px);}
}

@keyframes cubeg9 {
  0%{transform: translate(0px);}
  50%{transform: translate(0px, -400px);}
  100%{transform: translate(0px);}
}

// =======================

.aftericon{
  opacity: 0;
  width: 80px;height: 80px;
  position: fixed;top: 25px;left: 25px;
  z-index: 10;
  transition: all 0.2s linear 0s;
  //background: red;
   svg{
    z-index: 10;
    width: 100%;height: 100%;
   }
   &.on{
    opacity: 1;
    z-index: 10;
    transition: all 0.2s linear 0s;
    animation: smallani 4s linear 0s infinite;
    transform: rotate(0deg);
    svg{
      z-index: 10;
      width: 100%;height: 100%;
    }
   }
   @keyframes smallani {
    0%{transform: rotate(0deg);}
  25%{transform: rotate(10deg);}
  50%{transform: rotate(0deg);}
  75%{transform: rotate(-10deg);}
  100%{transform: rotate(0deg);}
   }
}
