@import url(common.scss);
$list-font:'S-CoreDream-3Light';
$content-font: 'SUITE-Regular';
$content-main-font:'SBAggroB';

@media screen and (min-width: 1365px) {
  body{
    font-size: 19px;
   // background: red;
  }
}
@media screen and (max-width: 1366px) and (min-width: 1024px) {
  body{
    background: #ff5e00;
    // 높이: 768최대
  }
}
@media screen and (max-width: 1023px) and (min-width: 768px) {
  body{
    background: yellow;
  }
}
@media screen and (max-width: 765px) and (min-width: 500px){
  body{
    background: blue;
  }
}


.container{
  font-family: $content-font;
  //background:linear-gradient(180deg, rgba(254,254,254,1) 43%, rgba(220,219,218,1) 100%);
  background-size: cover;
  background-attachment: fixed;
  width: 100%;
  height: 15000px;
  overflow-x: hidden;

  .index_list{
    //background: #fff2401c;
    position: relative;
    width: 100%;
    height: 100vh;
    opacity: 0;
    &.on{
      // 애니메이션-효과
      opacity: 1;

      // 글자 애니메이션
      .index_groub{
        width: 40%;
        height: 80vh;
        position: absolute;
        right: 0%;top: 50%;
        transform: translateY(-50%);
        font-family: $list-font;
        //background: red;
        ul{
          position: absolute;top: 10%;left: 20%;
          height: 90%;
          li{
            height: calc(100% / 8);
            font-size:1.7rem;
            opacity: 0;
            &::after{content: "";display: inline-block;background: #fff78781;
              width: 2.5rem;height: 2.5rem;
              position: absolute;top: -12%;left: -18px;
              z-index: -1;
            border-radius: 50%;;}
            span{
              font-size:1.2rem;
              font-weight: normal;
            }
            //transform: translateX(200px);
            &:nth-of-type(1){
              animation: listleft 1s ease-in 0s forwards;
            }
            &:nth-of-type(2){
              animation: listleft 1s ease-in 0.1s forwards;
            }
            &:nth-of-type(3){
              animation: listleft 1s ease-in 0.2s forwards;
            }
            &:nth-of-type(4){
              animation: listleft 1s ease-in 0.3s forwards;
            }
            &:nth-of-type(5){
              animation: listleft 1s ease-in 0.4s forwards;
            }
            &:nth-of-type(6){
              animation: listleft 1s ease-in 0.5s forwards;
            }
            &:nth-of-type(7){
              animation: listleft 1s ease-in 0.6s forwards;
            }
          }
           // 리스트 애니메이션 끝
        }
        @keyframes listleft {
          0%{transform: translateX(200px);opacity: 0;}
          100%{transform: translateX(0px);opacity: 1;}
        }
      }
    }
    // 애니메이션 끝

    .index_groub{
      width: 40%;
      height: 80vh;
      position: absolute;
      right: 0%;top: 10%;
      ul{
        position: absolute;top: 10%;left: 20%;
        height: 90%;
        li{
          height: calc(100% / 8);
          font-size: 1.7rem;
          font-weight: bold;
          transform: translate(200px);
          span{
            font-size:1rem;
            font-weight: normal;
          }
        }
      }
    }

  }



// section1
// 소개
.content1{
  position: relative;top: 0;left: 0;;
  width: 100%;height: 1100px;
  //background: #ffebeb;
  font-family: $content-font;
  opacity: 0;
  // 애니메이션 시작
  &.on{
    opacity: 1;
    .inform4{
      position: absolute;top: 0%;left: 0%;
      width: 90%;
      .p_groub1{
       // border: 1px solid #000;
        p{
          &:nth-of-type(1){
          font-family: $content-main-font;
          -webkit-text-stroke: 2px #000;
          color: transparent;
          font-size: 3.5rem;
         // border: 1px solid #000;
          overflow: hidden;
          span{
            display: inline-block;
            transform: translateY(90px);
            &:nth-of-type(1){
              transform: translateY(90px);
              animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 0s forwards;
            }
            &:nth-of-type(2){
              transform: translateY(90px);
              animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 0.2s 
              forwards;
            }
            &:nth-of-type(3){
              transform: translateY(90px);
              animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 0.4s 
              forwards;
            }
          }
          }
          &:nth-of-type(2){
          font-family: $content-main-font;
          font-size: 3.5rem;
          overflow: hidden;
          //border: 1px solid #000;overflow: hidden;
          span{
            display: inline-block;
            transform: translateY(90px);
            &:nth-of-type(1){
              transform: translateY(90px);
              animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 0s forwards;
            }
            &:nth-of-type(2){
              transform: translateY(90px);
              animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 0.2s 
              forwards;
            }
            &:nth-of-type(3){
              transform: translateY(90px);
              animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 0.4s 
              forwards;
            }
            &:nth-of-type(4){
              transform: translateY(90px);
              animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 0.6s 
              forwards;
            }
            &:nth-of-type(5){
              transform: translateY(90px);
              animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 0.8s 
              forwards;
            }
            &:nth-of-type(6){
              transform: translateY(90px);
              animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 1s 
              forwards;
            }
          }
          }
          }
        }
      }

      // 소제목 애니메이션
      .intd{
        opacity: 0;
        transform: translate(100px);
        //animation: leftani 1s ease-in-out 0s forwards;
        &:nth-of-type(1){
          animation: leftani 1s ease-in-out 0.3s forwards;
        }

        &:nth-of-type(2){
          animation: leftani 1s ease-in-out 0.6s forwards;
        }
      }
      @keyframes leftani {
        0%{opacity: 0;transform: translateX(100px);}
        100%{opacity: 1;transform: translateX(0px);}
      }
  }
